import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

import LineGraph from '../../components/lineGraph';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class WorkoutInfoGraph extends Component {
    render() {
        const { showDebug } = this.props;
        const { workoutStore } = this.props.stores;
        const { workout = {} } = workoutStore;
        const { gpx = {}, gpxInfo } = workout;
        const { gpxInfo: subGpxInfo = {} } = gpx;

        const finalGpxInfo = {
            ...subGpxInfo,
            ...gpxInfo,
        };
        const { speeds = [], elevations = [], temperatures = [] } = finalGpxInfo;

        let dataLeft = [];
        let dataLeft2 = [];
        let dataRight = [];
        const legendLeft = [];
        const legendLeft2 = [];
        const legendRight = [];
        const unitLeft = [];
        const unitLeft2 = [];
        const unitRight = [];

        if (elevations.length > 0) {
            const smoothed = mu.smoothOut(elevations, 0.85);
            const data = smoothed.map((e, idx) => ({
                x: idx,
                y: e,
            }));
            dataLeft.push(data);
            legendLeft.push('Elevation');
            unitLeft.push('m');
        }
        if (speeds.length > 0) {
            const smoothed = mu.smoothOut(speeds, 0.85);
            const data = smoothed.map((e, idx) => ({
                x: idx,
                y: e * 3.6,
            }));
            dataRight.push(data);
            legendRight.push('Speed');
            unitRight.push('km/t');
        }
        if (temperatures.length > 0) {
            const smoothed = mu.smoothOut(temperatures, 0.85);
            const data = smoothed.map((e, idx) => ({
                x: idx,
                y: e,
            }));
            dataLeft2.push(data);
            legendLeft2.push('Temp');
            unitLeft2.push('C');
        }

        const firstData = dataLeft[0] || [];
        const xTicks = [];
        if (firstData.length) {
            const partSize = Math.ceil(firstData.length / 4);
            const parts = Math.floor(firstData.length / partSize);
            if (showDebug) {
                console.log({ firstData, parts, partSize });
            }
            for (let i = 0, l = firstData.length; i < l; i += partSize) {
                xTicks.push(firstData[i].epoch);
            }
            xTicks.push(firstData[firstData.length - 1].epoch);
        }
        return (
            <>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={600}
                        height={200}
                        showXTicks={0}
                        // xTicksVal={val => util.isoDate(val)}
                        // xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        // xTicksLink={'/workouts/week/'}

                        dataLeft={dataLeft}
                        legendLeft={legendLeft}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={' m'}
                        unitLeft={unitLeft}
                        avgWindowLeft={[0, 0, 0]}
                        smoothLeft={[1, 1, 1]}


                        dataRight={dataRight}
                        legendRight={legendRight}
                        // dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
                        // legendRight={[`${seasonStart} ${ascend}`, `${seasonStart - 1} ${ascend}`]}
                        showYTicksRight={dataRight.length > 0 ? 1 : 0}
                        yTicksRightPostfix={' km/t'}
                        unitRight={unitRight}
                        avgWindowRight={[0, 0]}
                        smoothRight={[1, 1]}

                        // marker={week}
                    />
                </div>

                {dataLeft2.length > 0 && <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={600}
                        height={100}
                        showXTicks={0}
                        // xTicksVal={val => util.isoDate(val)}
                        // xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        // xTicksLink={'/workouts/week/'}

                        dataLeft={dataLeft2}
                        legendLeft={legendLeft2}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={' C'}
                        unitLeft={unitLeft}
                        avgWindowLeft={[0, 0, 0]}
                        smoothLeft={[1, 1, 1]}
                        // marker={week}
                    />
                </div>}

            </>
        );
    }
}

export default WorkoutInfoGraph;
